<template>
  <h1>Start Game</h1>
  <card-game
    v-for="(card, index) in onShowCardGame"
    :key="index"
    :numberCard="{ index, value: card }"
    @onFlipCard="checkRules($event)"
    :ref="`card-${index}`"
  />
</template>

<script>
import CardGame from "./CardGame.vue";

export default {
  components: {
    CardGame,
  },

  props: {
    onShowCardGame: {
      type: Array,
      default: function () {
        return [];
      },
    },
  },

  data() {
    return {
      rules: [],
    };
  },

  methods: {
    checkRules(card) {
      this.rules.push(card);

      if (this.rules.length > 2) return false;

      if (
        this.rules.length == 2 &&
        this.rules[0].value != this.rules[1].value
      ) {
        setTimeout(() => {
          this.$refs[`card-${this.rules[0].index}`][0].handelCloseCard();
          this.$refs[`card-${this.rules[1].index}`][0].handelCloseCard();
          this.rules = [];
        }, 800);
      } else if (
        this.rules.length == 2 &&
        this.rules[0].value == this.rules[1].value
      ) {
        this.$refs[`card-${this.rules[0].index}`][0].handelDisableCard();
        this.$refs[`card-${this.rules[1].index}`][0].handelDisableCard();

        this.rules = [];

        const totalElement = document.querySelectorAll(".disabled-card");

        if (totalElement.length == this.onShowCardGame.length - 2) {
          setTimeout(() => {
            this.$emit("onShowResult");
          }, 1500);
        }
      }
    },
  },
};
</script>

