<template>
  <h1>Day la trang project detail</h1>

  {{ project.name }}
  {{ project.email }}
</template>

<script>
export default {
  data() {
    return {
      project: null,
    };
  },

  created() {
    fetch(`https://jsonplaceholder.typicode.com/users/${this.$route.params.id}`)
      .then((response) => response.json())
      .then((json) => (this.project = json));
  },
};
</script>

