<template>
  <h1>Click select mode play game</h1>
  <div>
    <button @click="onSelectModeStartGame(16)">4 x 4</button>
    <button @click="onSelectModeStartGame(36)">6 x 6</button>
    <button @click="onSelectModeStartGame(64)">8 x 8</button>
    <button @click="onSelectModeStartGame(100)">10 x 10</button>
  </div>
</template>

<script>
export default {
  methods: {
    onSelectModeStartGame(totalBlock) {
      this.$emit("onSelectModeStartGame", { totalBlock });
    },
  },
};
</script>

