<template>
  <div class="flip-card" :class="{ 'disabled-card': disabled }">
    <div
      class="flip-card-inner"
      :class="{ 'flip-card-action': isFlipCard }"
      @click="handelFlipCard"
    >
      <div class="flip-card-front"></div>
      <div class="flip-card-back">
        {{ numberCard.value }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isFlipCard: false,
      disabled: false,
    };
  },

  props: {
    numberCard: {
      type: [String, Number, Array, Object],
      default: function () {
        return null;
      },
    },
  },

  methods: {
    handelFlipCard() {
      if (this.disabled) return false;

      this.isFlipCard = !this.isFlipCard;

      if (this.isFlipCard) this.$emit("onFlipCard", this.numberCard);
    },

    handelCloseCard() {
      this.isFlipCard = false;
    },

    handelDisableCard() {
      this.disabled = true;
    },
  },
};
</script>

<style scoped>
.flip-card {
  background-color: transparent;
  width: 200px;
  height: 250px;
  display: inline-block;
  margin: 10px;
  cursor: pointer;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 1s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flip-card .flip-card-action {
  transform: rotateY(-180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: #bbb;
  color: black;
}

.flip-card-back {
  background-color: #2980b9;
  color: white;
  transform: rotateY(-180deg);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
}

.disabled-card {
  cursor: default;
}
</style>