<template>
  <screen-main
    v-if="isPlaying == 'default'"
    @onSelectModeStartGame="onHandelBeforeStart($event)"
  />
  <play-game
    v-if="isPlaying == 'match'"
    :onShowCardGame="settingConfig.cardShuffle"
    @onShowResult="onshowResult"
  />

  <result-game
    v-if="isPlaying == 'result'"
    @onStartAgain="isPlaying = 'default'"
  />

  <form-register v-if="isPlaying == 'register'" />

  <router-view></router-view>
</template>

<script>
import { shuffle } from "./assets/utils";
import ScreenMain from "./components/ScreenMain.vue";
import PlayGame from "./components/PlayGame.vue";
import ResultGame from "./components/ResultGame.vue";
import FormRegister from './components/FormRegister.vue';

export default {
  name: "App",
  components: {
    ScreenMain,
    PlayGame,
    ResultGame,
    FormRegister,
  },

  data() {
    return {
      settingConfig: {
        totalBlockCard: null,
        cardShuffle: null,
      },

      isPlaying: "default",
    };
  },

  methods: {
    onHandelBeforeStart(config) {
      this.settingConfig.totalBlockCard = config.totalBlock;

      const firstArrCard = Array.from(
        { length: this.settingConfig.totalBlockCard / 2 },
        (_, i) => i + 1
      );
      const secondArrCard = [...firstArrCard, ...firstArrCard];

      this.settingConfig.cardShuffle = shuffle(
        shuffle(shuffle(shuffle(secondArrCard)))
      );
      this.isPlaying = "match";
    },

    onshowResult() {
      this.isPlaying = "result";
    },
  },
};
</script>

